import React, { Component } from 'react';
import './nav.css';

class Nav extends Component{
  render(){
    const navs = [
        {name: '', href: '/'},
        {name: '', href: '/'},
        {name: '""', href: '/'},
        {name: '', href: '/'},
        {name: '', href: '/'},
        {name: '', href: '/'},
        {name: '', href: '/'},
        {name: '', href: '/'}
      ];
    return(
      <div className='nav'>
        <ul>
          {
            navs.map((menu, index) => (
              <li key={index}>
                <a className={index === 0 ? 'active' : ''} href={menu.href}>{menu.name}</a>
              </li>
            ))
          }
        </ul>
      </div>
    )
  }
}
export default Nav;


